<template>
    <div id="mainCoponent">
        <div class="layout">
            <Layout :style="{minHeight: '100vh'}">
                <Sider collapsible :collapsed-width="78" v-model="isCollapsed">
                    <Menu @on-select="onMenuSelect" active-name="1" theme="dark" width="auto" :class="menuitemClasses">
                        <MenuItem name="1">
                            <Icon type="pricetag"></Icon>
                            文章管理
                        </MenuItem>                        
                        <MenuItem name="2">
                            <Icon type="person-stalker"></Icon>
                            成员管理
                        </MenuItem>                        
                        <MenuItem name="3">
                            <Icon type="ios-book"></Icon>
                             成果管理
                        </MenuItem>                                       
                        <MenuItem name="4">
                            <Icon type="folder"></Icon>
                            资源管理
                        </MenuItem>                   
                        <MenuItem name="5">
                            <Icon type="ios-paperplane"></Icon>
                            公告管理
                        </MenuItem> 
                        <MenuItem name="6">
                            <Icon type="images"></Icon>
                            轮播图管理
                        </MenuItem>                      
                    </Menu>
                </Sider>
                <Layout>
                    <Header class="title" :style="{background: '#fff', boxShadow: '0 2px 3px 2px rgba(0,0,0,.1)'}">  
                        <div class="sign">web应用团队后台管理</div>      
                        <div><Button type="error" @click="logOut">退出</Button> </div>                         
                    </Header>
                    <Content>
                        <router-view></router-view>  
                    </Content>
                    <Footer class="layout-footer-center">
                    CopyRight©2017 <br>
                    PZHU-WEB   蜀ICP备17013737号<br>
                    地址：攀枝花市东区机场路10号   邮编：617000   联系方式：lanquanxiang@gmail.com
                    </Footer>
                </Layout>
            </Layout>
        </div>
    </div>  
</template>

<script>
export default {
    name: 'mainCoponent',
    data () {
    return {
        isCollapsed: false,
        routers: ['/back/article','/back/members','/back/achievement','/back/file','/back/notice','/back/banner']
    };
  },
  computed: {
    menuitemClasses: function () {
      return [
          'menu-item',
          this.isCollapsed ? 'collapsed-menu' : ''
      ]
    }
  },
  methods:{
      onMenuSelect(name){
        this.$router.push(this.routers[parseInt(name)-1]);
      },
      logOut(){
          sessionStorage.removeItem('token');
          this.$Message.success("安全退出");
          this.$router.push('/login');
      }
  }
}
</script>
<style lang="scss" scoped>
.layout-con{
    height: 100%;
    width: 100%;
}
.menu-item span{
    display: inline-block;
    overflow: hidden;
    width: 69px;
    text-overflow: ellipsis;
    white-space: nowrap;
    vertical-align: bottom;
    transition: width .2s ease .2s;
}
.menu-item i{
    transform: translateX(0px);
    transition: font-size .2s ease, transform .2s ease;
    vertical-align: middle;
    font-size: 16px;
}
.collapsed-menu span{
    width: 0px;
    transition: width .2s ease;
}
.collapsed-menu i{
    transform: translateX(5px);
    transition: font-size .2s ease .2s, transform .2s ease .2s;
    vertical-align: middle;
    font-size: 22px;
}

.layout-footer-center{
    text-align: center;
}

.title{
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 18px;
  .sign{
    font-weight: bold;
    font-size: 26px;
  }
}
</style>




